-----------------------------------------------------

**/
button:focus, button:active {
  outline: none;
  box-shadow: none;
  border: none; }

.btn {
  -webkit-appearance: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: inline-block;
  background: orange;
  border: 1px solid orange;
  border-radius: 3px;
  color: #FFFFFF;
  cursor: pointer;
  font-size: 1.6em;
  line-height: 1;
  padding: 1.25em 1.65em 1em;
  text-transform: uppercase;
  text-decoration: none;
  transition: background .15s ease; }
  .btn:hover, .btn:focus, .btn:active {
    background: #e69500;
    outline: none;
    opacity: 1; }
  .btn.btn--small {
    font-size: 1.4em;
    padding: .75em 2em .65em; }
  .btn.borderless {
    border: none;
    line-height: 1.1;
    padding-left: 0;
    padding-right: 0; }
  .btn.full-width {
    display: block; }

