/**

-----------------------------------------------------
  elements/_headings.scss
-----------------------------------------------------

**/

h1,h2,h3,h4,h5,h6 {
    &.dark {
        color: $black;
    }
    &.white {
        color: $white;
    }
}

// H1
h1, .h1 {
    font-size: 2.8em;
    line-height: 1.15em;
    margin-bottom: 1em;
}

// H2
h2, .h2 {
    font-size: 2em;
    line-height: 1.6em;
    margin-bottom: 1em;
    text-transform: none;
    &.no-top-margin {
        margin-top: 0;
    }
    &.capped-width {
        max-width: 550px;
    }
}

// H3
h3, .h3 {
    font-size: 1.8em;
    line-height: 1.5;
    margin-bottom: 1em;
    &.large {
        font-size: 2.4em;
    }
}

// H4
h4, .h4 {
    margin-bottom: 1em;
}

// H5
h5, .h5 {
    margin-bottom: 1em;
}

// H6
h6, .h6 {
    margin-bottom: 1em;
}